/* modal */
.contactfrm-popup-main .modal-dialog button.close {
    position: absolute;

    top: -22px;

    right: -22px;

    padding: 0;

    width: 3.063rem;

    height: 3.063rem;

    border: solid 5px #1e1e51;

    background: white;

    border-radius: 50%;

    color: #1e1e51;

    text-align: center;

    font-family: initial;

    font-size: 30px;

    opacity: 1;

    line-height: normal;

    z-index: 1;

    text-shadow: 0 0 0;

    cursor: pointer;
}

.contactfrm-popup-main {
    padding: 0 !important;
}
.form-label {
    margin-bottom: 0.5rem;
}
.contactfrm_pop h2 {
    color: #1e1e51;
    text-align: center;
    font-size: 26px;
    font-weight: 700;
    font-family: var(--source-sans-pro);
    text-transform: capitalize;
    line-height: 1.5;
    margin-bottom: 20px;
}
.contactfrm_pop label {
    font-weight: 700;
    color: #1e1e51;
}

.contactfrm_pop ul li {
    float: left;

    width: 100%;

    margin-bottom: 15px;
}

.contactfrm_pop ul li label {
    color: #000000;

    font-size: 18px;

    margin-bottom: 8px;

    font-family: var(--source-sans-pro);

    font-weight: 500;

    float: left;

    width: 100%;
}

.contactfrm_pop ul li input {
    float: left;

    width: 100%;

    font-size: 15px;

    padding: 0 10px;

    font-size: 15px;

    border: 1px solid black;

    height: 40px;

    color: black;
}

#submitBtn {
    color: white;
